// 상단에서 React 가져오기
const React = require('react') as typeof import('react');

// testing-library 관련 설정
let configure: any;
try {
  const rtl = require('@testing-library/react-native');
  configure = rtl.configure;
  require('@testing-library/jest-native/extend-expect');
  
  if (configure) {
    configure({
      asyncUtilTimeout: 10000,
    });
  }
} catch (error) {
  console.warn('Testing library not loaded:', error);
}

// FireEvent 관련 오류 해결을 위한 설정
// FireEvent 관련 오류 해결을 위한 설정
jest.mock('@testing-library/react-native', () => {
  const actual = jest.requireActual('@testing-library/react-native');
  
  // fireEvent 메서드 오버라이드
  const fireEvent = {
    ...actual.fireEvent,
    press: jest.fn((element) => {
      if (element && element.props && typeof element.props.onPress === 'function') {
        element.props.onPress();
        return true;
      }
      console.warn('오류: 요소에 onPress 핸들러가 없습니다');
      return false;
    }),
    changeText: jest.fn((element, text) => {
      if (element && element.props && typeof element.props.onChangeText === 'function') {
        element.props.onChangeText(text);
        return true;
      }
      console.warn('오류: 요소에 onChangeText 핸들러가 없습니다');
      return false;
    })
    };
    
    return {
      ...actual,
      fireEvent,
      render: (component: React.ReactElement) => {
        const result = actual.render(component);
        
        // getByTestId 메서드에 대한 테스트 인스턴스 추가
        result.getByTestId = (testId: string) => {
          const elements = result.queryAllByTestId(testId);
          if (elements.length === 0) {
            throw new Error(`테스트 ID '${testId}'를 가진 요소를 찾을 수 없습니다`);
          }
          return elements[0];
        };
        
        return result;
      }
    };
  });

// Reanimated 모킹
