// React Native Vector Icons 모킹
jest.mock('react-native-vector-icons/MaterialCommunityIcons', () => ({
  __esModule: true,
  default: 'MockedMaterialCommunityIcons',
}));

// React Native Paper 모킹 개선
jest.mock('react-native-paper', () => {
  const React = require('react');
  const RN = require('react-native');
  
  return {
    TextInput: function MockTextInput(props: any) {
      const { label, testID, onChangeText, value, ...restProps } = props;
      return React.createElement(RN.TextInput, {
        testID: testID || 'text-input',
        onChangeText: onChangeText,
        value: value,
        placeholder: label || props.placeholder,
        accessibilityLabel: label,
        editable: props.editable !== false,
        accessible: true,
        ...restProps
      });
    },
    Button: function MockButton(props: any) {
      return React.createElement(RN.TouchableOpacity, 
        { 
          testID: props.testID,
          onPress: props.onPress,
          accessible: true,
          accessibilityRole: 'button',
          ...props 
        },
        React.createElement(RN.Text, null, props.children)
      );
    },
    Chip: function MockChip(props: any) {
      return React.createElement(RN.TouchableOpacity, 
        { 
          testID: props.testID,
          onPress: props.onPress,
          style: [{ borderRadius: 20, padding: 8, margin: 4 }, props.style],
          ...props 
        },
        React.createElement(RN.Text, null, props.children)
      );
    },
    Text: function MockText(props: any) {
      return React.createElement(RN.Text, props);
    },
    ActivityIndicator: function MockActivityIndicator(props: any) {
      return React.createElement(RN.ActivityIndicator, props);
    },
    Provider: function MockProvider({ children }: { children: any }) {
      return children;
    },
  };
});

// FireEvent 관련 오류 해결
jest.mock('@testing-library/react-native', () => {
  const actual = jest.requireActual('@testing-library/react-native');
  
  return {
    ...actual,
    fireEvent: {
      ...actual.fireEvent,
      press: jest.fn((element) => {
        if (element && element.props && typeof element.props.onPress === 'function') {
          element.props.onPress();
          return true;
        }
        return false;
      }),
      changeText: jest.fn((element, text) => {
        if (element && element.props && typeof element.props.onChangeText === 'function') {
          element.props.onChangeText(text);
          return true;
        }
        return false;
      })
    }
  };
});

// 콘솔 오류 메시지 필터링
const originalConsoleError = console.error;
console.error = (...args) => {
  if (
    typeof args[0] === 'string' &&
    (args[0].includes('act(') || 
     args[0].includes('inside a test was not wrapped in act') ||
     args[0].includes('unmounted component') ||
     args[0].includes('감정 로드 오류') ||
     args[0].includes('챌린지 데이터 로딩 오류') ||
     args[0].includes('Error: API 오류'))
  ) {
    return; // 특정 오류 메시지 무시
  }
  originalConsoleError(...args);
};